footer
    background-color: #000
    .container
        display: flex
        padding: 100px
        justify-content: space-between
        // align-items: center
        line-height: 40px

        a
            text-decoration: none
            color: #fff
            display: block
            &:hover
                color: #ddd

        ul
            display: flex
            align-items: flex-start
            flex-direction: column

            li
                list-style: none

        span
            color: #ffffffb0

        nav
            display: flex
            flex-direction: column
@media( max-width: 600px )
    footer
        .container
            flex-wrap: wrap
            padding: 40px
            line-height: 25px

            a
                font-size: 12px

            span
                margin: 5px 0
                font-size: 12px