header
    .container
        display: flex
        justify-content: space-between
        padding: 20px
        border-bottom: 1px solid #000

        a
            transition: 0.3s
            text-decoration: none
            color: #000
            font-size: 20px
            display: flex
            align-items: center
            letter-spacing: 2px

            svg
                margin: 0 5px

            &:hover
                color: #0000009c

@media( max-width: 600px )
    header
        .container
            padding: 10px

            a
                font-size: 16px
                letter-spacing: 2px
