.Boxes
  margin-bottom: 50px
  .container
    display: flex
    flex-wrap: wrap

    div
      transition: 0.3s
      padding: 10px
      width: calc( 100% / 3 - 20px )
      border: 1px solid #000
      margin: 10px 10px

      &:hover
        transform: scale(1.05)

      h2
        margin-bottom: 10px

@media( max-width: 600px )
  .Boxes
    .container

      div
        transition: 0.3s
        padding: 10px
        width: calc( 100% / 2 - 20px )
        border: 1px solid #000
        margin: 10px 10px

        &:hover
          transform: scale(1.05)

        h2
          margin-bottom: 10px
          font-size: 16px

        p
          font-size: 12px
